import { ExchangeModal } from '@components/Modals/Exchange';
import { useModal } from '@hooks/useModal';
import Dollar from '@images/quotes/Dollar.svg';

import { CardContainer, Image, Info, Rate, Title } from './styled';
import { FinancialCardProps } from './types';

export const FinancialCard = ({ financialItem }: FinancialCardProps) => {
  const { name, price_usd, icon_url } = financialItem;
  const { isOpen, handleOpen, handleClose, modalRef } = useModal();

  return (
    <>
      <CardContainer onClick={handleOpen}>
        <Image src={icon_url ? icon_url : Dollar} alt={name} />
        <Info>
          <Title>{name}</Title>
          <Rate>{price_usd && '$ ' + price_usd.toFixed(2)}</Rate>
        </Info>
      </CardContainer>

      {isOpen && (
        <ExchangeModal
          onClose={handleClose}
          name={name}
          price_usd={'$ ' + price_usd.toFixed(2)}
          icon_url={icon_url ? icon_url : Dollar}
          modalRef={modalRef}
        />
      )}
    </>
  );
};
