import { createPortal } from 'react-dom';

import {
  CloseButton,
  Image,
  Info,
  ModalContainer,
  Overlay,
  Rate,
  Title,
} from './styled';
import { ExchangeModalProps } from './types';

export const ExchangeModal = (props: ExchangeModalProps) => {
  // TODO [https://sharing.clickup.com/9012257271/t/h/86963qyyb/S6H6HENTBZC8UVT] - Modal elastic search and input field

  const { onClose, name, price_usd, icon_url, modalRef } = props;

  return createPortal(
    <Overlay>
      <ModalContainer ref={modalRef}>
        <Image src={icon_url} alt={name} />
        <Info>
          <Title>{name}</Title>
          <Rate>{price_usd}</Rate>
        </Info>
        <CloseButton onClick={onClose}>Close</CloseButton>
      </ModalContainer>
    </Overlay>,
    document.body,
  );
};
