import { fetchCurrenciesThunk } from '@store/slices/currencySlice';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { AppDispatch, RootState } from '@/store';

export const useCurrencies = () => {
  const [lastUpdated, setLastUpdated] = useState<string | null>(null);

  const dispatch = useDispatch<AppDispatch>();
  const { currencies, isLoading, error } = useSelector(
    (state: RootState) => state.currencies,
  );

  useEffect(() => {
    const fetchData = () => {
      dispatch(fetchCurrenciesThunk());
      const now = new Date();
      setLastUpdated(
        `${now.getHours()}:${String(now.getMinutes()).padStart(2, '0')}`,
      );
    };

    fetchData();

    const intervalId = setInterval(fetchData, 60 * 1000);

    return () => clearInterval(intervalId);
  }, [dispatch]);

  return { currencies, isLoading, error, lastUpdated };
};
